        </div>
      </div>

      <div class="py-3 px-4" style="height: 100vh">
        <!-- row -->
        <div class="rs-members__list pt-2">
          <transition-group name="list" class="row">
            <div v-for="(member, index) in members" :key="index" class="col-lg-4 col-sm-6">
              <div class="media mb-4 align-items-center" v-if="!member.inMembers">
                <i class="fa fa-users mr-3 rs-award"></i>

                <div class="media-body d-flex">
                  <div class="d-flex flex-column">

                    <!-- Member Link -->
                    <a href="home.html" class="h5 font-weight-bold rs-mb-2 rs-text-black rs-text-danger-hover">
                      {{member.name}}
                    </a>
                    <span class="h6 text-dark-grey mb-1">Group</span>
                  </div>
                  <!-- Dropdown | Actions -->
                  <div @click="animationMenuUpdate" class="dropdown ml-auto rs-dropdown-h20 mt-2">
                    <a id="moreDropdown9Invoker" class="u-link-v5 g-dot_wrap d-flex align-items-center" href="#!"
                       aria-haspopup="true"
                       aria-expanded="false" data-toggle="dropdown">
                      <span class="g-dot dot-1 rs-dot-grey mb-3"></span>
                      <span class="g-dot dot-2 rs-dot-grey mb-3"></span>
                      <span class="g-dot dot-3 rs-dot-grey mb-3"></span>
                    </a>
                    <div class="rs-dropdown dropdown-menu dropdown-menu-right" aria-labelledby="moreDropdown9Invoker">
                      <div class="rs-dropdown-content">
                        <h5 class="rs-dropdown__title">Actions</h5>
                        <a class="rs-dropdown__item" href="#">
                          <i class="fa fa-edit rs-dropdown__item-icon"></i>
                          Edit
                        </a>
                        <a class="rs-dropdown__item" href="#">
                          <i class="reso-icon-pin rs-dropdown__item-icon"></i>
                          Pin
                        </a>
                        <!-- Delete Member -->
                        <a class="rs-dropdown__item" href="#!" @click="deleteMember(index)">
                          <i class="fa fa-trash rs-dropdown__item-icon"></i>
                          Delete
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="media mb-4 align-items-center" v-else >
                <a href="home.html" class="mr-3 rs-award">
                  <!-- Avatar Member -->
                  <img class="rounded-circle" :src="member.avatar" width="45" height="45" alt="Image Description">
                </a>
                <div class="media-body d-flex">
                  <div class="d-flex flex-column">
                    <a href="home.html" class="h5 font-weight-bold mb-1 rs-text-black rs-text-danger-hover">{{member.name}}
                      {{member.secondName}}</a>
                    <span class="h6 text-dark-grey mb-1">Chargé de mission</span>
                    <!-- Member Badge -->
                    <span class="rs-badge rs-badge-grey rs-badge-grey--hover rs-width-60" data-toggle="tooltip"
                          data-placement="top"
                          title="Direction Régionale de l'Alimentation de l'Agriculture et de la Forêt">{{member.badge}}</span>
                  </div>
                  <!-- Dropdown | Actions -->
                  <div @click="animationMenuUpdate" class="dropdown ml-auto rs-dropdown-h20 mt-2">
                    <a id="moreDropdown9Invoker" class="u-link-v5 g-dot_wrap d-flex align-items-center" href="#!"
                       aria-haspopup="true"
                       aria-expanded="false" data-toggle="dropdown">
                      <span class="g-dot dot-1 rs-dot-grey mb-3"></span>
                      <span class="g-dot dot-2 rs-dot-grey mb-3"></span>
                      <span class="g-dot dot-3 rs-dot-grey mb-3"></span>
                    </a>
                    <div class="rs-dropdown dropdown-menu dropdown-menu-right" aria-labelledby="moreDropdown9Invoker">
                      <div class="rs-dropdown-content">
                        <h5 class="rs-dropdown__title">Actions</h5>
                        <a class="rs-dropdown__item" href="#">
                          <i class="fa fa-edit rs-dropdown__item-icon"></i>
                          Edit
                        </a>
                        <a class="rs-dropdown__item" href="#">
                          <i class="reso-icon-pin rs-dropdown__item-icon"></i>
                          Pin
                        </a>
                        <!-- Delete Member -->
                        <a class="rs-dropdown__item" href="#!" @click="deleteMember(index)">
                          <i class="fa fa-trash rs-dropdown__item-icon"></i>
                          Delete
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {{animationMenuUpdate()}}
            </div>
          </transition-group>
        </div>
        <!-- END row -->
      </div>

    </div>
  </div>
</template>

<script>
  import MemberSelectMenu from "./MemberSelectMenu";

  export default {
    name: "Members",
    components: {MemberSelectMenu},
    data() {
      return {
        members: []
      }
    },
    methods: {
      // Add member
      addMember(member) {
        this.members.push(member);
      },
      // Delete member
      deleteMember(index) {
        this.members.splice(index, 1);
      },
      // Init slide animation
      animationMenuUpdate() {
        var $dropdown = $('.dropdown');

        $dropdown.on('show.bs.dropdown', function (e) {
          $(this).find('.dropdown-menu').slideDown(300);
        });

        $dropdown.on('hide.bs.dropdown', function (e) {
          $(this).find('.dropdown-menu').slideUp(200);
        });
      }
    }
  }
</script>

