<template>
  <div v-click-outside="outside" class="rs-form-group and-has-icon form-group mb-0 rs-member-select position-relative">
      <span class="select2 select2-container select2-container--default rs-select-member w-100">
        <span class="selection">
          <!--Select menu input active = false-->
          <span v-if="!active" class="select2-selection select2-selection--single" @click="openMenu">
            <span class="members-menu-placeholder">{{placeholder}}</span>
            <span class="select2-selection__arrow" role="presentation"><b role="presentation"></b></span>
          </span>

          <!--Select menu input active = true-->
          <span v-if="active" class="select2-selection select2-selection--single">
            <input v-model="search" v-focus class="members-menu-input" placeholder="Saisir pour filtrer la liste"
                   type="search" tabindex="0" autocomplete="off" autocorrect="off" autocapitalize="none"
                   spellcheck="false" role="textbox">
            <span class="select2-selection__arrow" role="presentation" @click="active = !active"><b
              style="transform: rotate(180deg)" role="presentation"></b></span>
          </span>
      </span>
    </span>

    <!--Select menu when active-->
    <div v-show="active">
      <div id="mCSB_1" class="members-menu-sresults mCustomScrollBox mCS-light mCSB_vertical mCSB_inside">
        <ul class="select2-results__options">

          <!-- Loading icon -->
          <img v-if="loading"
               style="position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 150px; z-index: 1000"
               src="assets/img/Loading_icon.gif" alt="loadin animation">


          <!-- Animation group for items -->
          <transition-group name="staggered-fade"
                            v-bind:css="false"
                            v-on:before-enter="beforeEnter"
                            v-on:enter="enter"
                            v-on:leave="leave"
                            tag="div">

            <!-- Member Item -->
            <li v-for="(member, index) in filterList" :key="index" class="select2-results__option" :data-index="index">
              <!-- IF Group -->
              <div class="media mb-1 align-items-start members-group" v-if="!member.inMembers">
                <i class="fa fa-users mr-3 rs-award"></i>

                <div class="media-body d-flex">
                  <div class="d-flex flex-column">

                    <!-- Member Link -->
                    <a href="home.html" class="h5 font-weight-bold rs-mb-2 rs-text-black rs-text-danger-hover">
                      {{member.name}}
                    </a>
                    <span class="h6 text-dark-grey mb-1">Group</span>
                  </div>
                </div>
                <!-- Add Member Icon -->
                <div class="icn-add mr-2" @click="$emit('addMember', filterList[index])"></div>
              </div>


              <div class="media mb-1 align-items-start" v-else>
                <a href="home.html" class="mr-3 rs-award">
                  <!-- Avatar -->
                  <img class="rounded-circle" :src="member.avatar" width="45" height="45" alt="Image Description">
                </a>

                <!-- Member Content -->
                <div class="media-body d-flex">
                  <div class="d-flex flex-column">

                    <!-- Member Link -->
                    <a href="home.html" class="h5 font-weight-bold rs-mb-2 rs-text-black rs-text-danger-hover">{{member.name}} {{member.secondName}}</a>
                    <span class="h6 text-dark-grey mb-1">Charge de mission 1</span>

                    <!-- Badge | Init Tooltip -->
                    {{tooltipInit()}}
                    <span class="rs-badge rs-badge-grey rs-badge-grey--hover rs-width-60" data-toggle="tooltip"
                          data-placement="top" title=""
                          data-original-title="Direction Régionale de l'Alimentation de l'Agriculture et de la Forêt">{{member.badge}}</span>
                  </div>
                </div>

                <!-- Add Member Icon -->
                <div class="icn-add mr-2" @click="$emit('addMember', filterList[index])"></div>
              </div>
            </li>
          </transition-group>
        </ul>
      </div>
      <!-- Invite Block -->
      <div class="rs-inviter-block"><p class="mb-3">La personne que vous souhaitez ajauter au project nefait pa parti du
        Reso?</p>
        <button class="btn btn-danger">Inviter un nouvel utilisateur</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "MemberSelectMenu",
    props: ['placeholder'],
    data() {
      return {
        active: false, // true when menu open
        search: '', // search string
        members: [], // array with members
        loading: false, // loading (ture when members is loading)
        inter: 0 // integer for animation delay when scroll members menu
      }
    },
    components: {
      PerfectScrollbar
    },
    methods: {
      // Event when menu is open
      openMenu() {
        this.active = !this.active;
        this.fetchMembers();
      },

      // Load more members
      loadMore() {
        if (!this.loading) {
          this.loading = true;
          axios.get('http://vh228-reso.ffox.site/members.json')
            .then(response => {
              setTimeout(() => {
                // Push new members into array
                response.data.forEach(member => {
                  this.members.push(member)
                });

                this.loading = false;
              }, 500)
            })
        }
      },
      // If Click outside
      outside() {
        this.active = false;
      },

      // Init bootstrap tooltip when member pushed to members array and render in list
      tooltipInit() {
        setTimeout(function () {
          $('[data-toggle="tooltip"]').tooltip({boundary: 'viewport'});
        }, 500)
      },

      // Fetch members when menu is open
      fetchMembers() {
        if (this.members.length === 0) {
          // Start loading
          this.loading = true;

          setTimeout(() => {
            axios.get('http://vh228-reso.ffox.site/members.json')
              .then(response => {
                this.members = response.data;
              });

            // End loading
            this.loading = false
          }, 500)
        }
      },

      // Animation for each item in members (lazyload) before animation
      beforeEnter: function (el) {
        el.style.opacity = 0
        Velocity(el, {scale: .7}, {duration: 0})
      },

      // In animation
      enter: function (el, done) {
        this.animationDelayr(15);
        var delay = this.inter * 150
        setTimeout(function () {
          Velocity(
            el,
            {opacity: 1, scale: 1},
            {complete: done}
          )
        }, delay)

        delay = 0;
      },

      // When leave item
      leave: function (el, done) {
        var delay = el.dataset.index * 50
        setTimeout(function () {
          Velocity(
            el,
            {opacity: 0},
            {complete: done}
          )
        }, delay)
      },

      // Set delay animation for new items in members (lazyload)
      animationDelayr(perItems) {
        if (this.inter >= perItems) {
          this.inter = 0;
        }
        this.inter++;
      },
    },
    computed: {
      // Filter members (demo)
      filterList() {
        var vm = this
        return this.members.filter(function (item) {
          return item.name.toLowerCase().indexOf(vm.search.toLowerCase()) !== -1
        })
      }
    },
    mounted() {
      // Add event scroll (when members menu on the bottom call loadMore function)
      var scrollerMenu = document.querySelector('#mCSB_1');
      scrollerMenu.addEventListener('scroll', e => {
        if (scrollerMenu.scrollTop + scrollerMenu.clientHeight >= scrollerMenu.scrollHeight) {
          this.loadMore();
        }
      })

      // Init PerfectScrollbar.js scrollbar
      var scrollThirdInit = new PerfectScrollbar(scrollerMenu);

      // Init tooltips for another elements onthe page
      $('[data-toggle="tooltip"]').tooltip()
    },
    directives: {
      focus: {
        // directive definition
        inserted: function (el) {
          el.focus()
        }
      },

      'click-outside': {
        bind: function (el, binding, vNode) {
          // Provided expression must evaluate to a function.
          if (typeof binding.value !== 'function') {
            const compName = vNode.context.name
            let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
            if (compName) {
              warn += `Found in component '${compName}'`
            }

            console.warn(warn)
          }
          // Define Handler and cache it on the element
          const bubble = binding.modifiers.bubble
          const handler = (e) => {
            if (bubble || (!el.contains(e.target) && el !== e.target)) {
              binding.value(e)
            }
          }
          el.__vueClickOutside__ = handler

          // add Event Listeners
          document.addEventListener('click', handler)
        },

        unbind: function (el, binding) {
          // Remove Event Listeners
          document.removeEventListener('click', el.__vueClickOutside__)
          el.__vueClickOutside__ = null

        }
      }
    }
  }
</script>

